import {useEffect, lazy, Suspense, useState} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link, useLocation, useParams } from 'react-router-dom';

import logo from "../pages/image/logo.svg"
import logos from "../pages/image/lhome.png"
import {home} from 'react-icons-kit/fa/home'
import {useDispatch, useSelector} from 'react-redux';
import { post, fetchData } from '../application/middlewares/index';
import { Icon } from 'react-icons-kit'
// import {home} from 'react-icons-kit/fa/home';



const Navbar = lazy(() => import('./Navbar'))
const Footer = lazy(() => import('./Footer'))
const Maps = lazy(() => import('./Maps'))


function AllMap(){
    const dispatch = useDispatch();
    
// console.log(parsed.lan);

    const [locations, setLocations] = useState();
    const location = useLocation()
    const queryString = require('query-string');
    const parsed = queryString.parse(location.search);
    const [lat, setLat] = useState(parsed.lat)
    const [lng, setLng] = useState(parsed.lng)

    useEffect(()=>{
            
    }, [location.search])
    useEffect(()=>{
        console.log(location.search)
        dispatch(fetchData({
            url:`tm/get-real-estate-positions${location.search}`,
            token:'',
            action:(response)=>{
                if(response.success){
                    console.log("get_positions ",response)
                    // console.log(response.data.rows);
                    setLocations(response.data.rows);
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    },[])
    // console.log(locations);
       
    return(
        <main>
            <Navbar />
            <hr />
            <Maps locations={locations} lat = {lat} lng = {lng}/>
            <Footer />
        </main>

    )
}

export default AllMap;